import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController, LoadingController, IonSearchbar } from '@ionic/angular';
import { AppMin, WitePrompt } from '../core';
import { Router, ActivatedRoute } from '@angular/router'

var SearchPage_message;
let SearchPage_close;


@Component({
  selector: 'app-search',
  templateUrl: './search.page.html',
  styleUrls: ['./search.page.scss'],
})
export class SearchPage implements OnInit {
  @ViewChild(IonSearchbar) searchbar1: IonSearchbar;
  loader;
  header_class: string = "";

  searchQuery: string = '';
  items: string[];
  objlist = [];
  search: string;
  notMoer = 1; //是否显示 没有找到
  sendDataChange;
  constructor(public navCtrl: NavController, public Router: Router, public ActivatedRoute: ActivatedRoute, public appMin: AppMin, public loadingCtrl?: LoadingController) {

    let key = this.ActivatedRoute.snapshot.queryParams['key']
    console.log("key=" + key);
    if (key != "") {
      this.search = key;
      this.addSearch(key);
    }


    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      //  console.log("判断为安卓系统 设置状态栏样式")
    }


    let _this = this;
    

    setTimeout(() => {
      _this.searchbar1.setFocus();
    }, 300);
  }

  async send(value, ishow?: boolean) {



    if (ishow) {
      this.loader = await this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000
      });
      this.loader.present();
    }
    this.appMin.get(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }

  ionViewDidEnter() {
    //每次进入页面

    console.log('进入搜索页面');


    setTimeout(() => {
      //  this.searchbar1.setFocus();
    }, 550)
    console.log('搜索页面设置搜索焦点完毕');
  }

  //自动完成
  getItems(ev: any) {
    console.log("getItems")


  }
  search_btn() {
    if (this.search != '') {
      WitePrompt('发送请求');
      this.addSearch(this.search);
    }
  }


  huitui() {

    //单击取消搜索返回按钮
    //  alert('返回');
    this.navCtrl.pop();
  }
  addSearch(key?: any) {
    //发送搜索请求

    if (this.appMin.trim(key) != "") {
      let obj = {
        type: 'search',
        key: key

      }

      this.send(JSON.stringify(obj), true);
      console.log("send_key=" + key);
    }
    else {
      console.log("关键字为空");
    }

  }
  listClick(ev: any) {
    //咨询师列表被单击
    console.log(ev.ObjID);
    var obj = {
      animation: "ios-transition"  //设置过度效果
    }
    let data = {
      userId: ev.ObjID
    }

    // this.navCtrl.push(Consultant, data, obj);
    this.Router.navigate(['consultant'], { queryParams: data });

  }
  dataHandle(meg: any) {
    let this1 = this;
    if (meg['type'] == 'search_ret') {
      this.objlist = meg['rows'];
      WitePrompt('_this.objlist =' + JSON.stringify(meg['rows']))
    }
    setTimeout(() => {
      this.searchbar1.setFocus();
    }, 200)
    console.log('搜索页面设置搜索焦点完毕');

    if (this.objlist.length == 0) {
      console.log('没有搜索到');
      this.notMoer = 0;
    }

  }

  ngOnInit() {
  }

}
